.button {
  padding: .5rem 1rem;
  display: inline-block;
  cursor: pointer;
  color: var(--text-color-lighter);
  border: none;
  background-color: transparent;
  font-size: var(--h3-font-size);
  margin-top: 1rem;

  &-success {
    border-bottom: 2px solid var(--primary-color);
  }
}